import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  FormsModule, ReactiveFormsModule
} from '@angular/forms';
import { SelectMode } from '@danielmoncada/angular-datetime-picker';
import { EventbusService, SoloDateComponent, TooltipComponent } from '@front-shared-lib';

export interface IsearchSelectOptions {
  label: string;
  value: string;
}
export interface IsearchTerm {
  [x: string]: string;
}
@Component({
  selector: 'app-admin-date-filter-field',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    SoloDateComponent,
    TooltipComponent
  ],
  templateUrl: './admin-date-filter-field.component.html',
  styleUrl: './admin-date-filter-field.component.scss'
})
export class AdminDateFilterFieldComponent {
  @Input() placeholder = 'Date Filter...';
  @Output() filter: EventEmitter<Date | undefined> = new EventEmitter<Date | undefined>();
  @Output() filterRange: EventEmitter<[Date, Date] | undefined> = new EventEmitter<[Date, Date] | undefined>();
  @Input() selectMode: SelectMode = 'range';

  constructor(private eventbus: EventbusService) {}

  clearRange() {
    this.eventbus.generalEvent$.next({
      type: 'clearRangeDate',
      data: true
    });
  }
}
